<template>
  <div class="ratingselect">
  	<div class="rating-type border-1px">
  		<span @click="select(2, $event)" class="block positive" :class="{'active': selectType===2}">{{desc.all}}<span class="count">{{ratings.length}}</span></span>
  		<span @click="select(0, $event)" class="block positive" :class="{'active': selectType===0}">{{desc.positive}}<span class="count">{{positives.length}}</span></span>
  		<span @click="select(1, $event)" class="block negative" :class="{'active': selectType===1}">{{desc.negative}}<span class="count">{{negatives.length}}</span></span>
  	</div>
  	<div @click="toggleContent" class="switch" :class="{'on': onlyContent}">
  		<span class="icon-check_circle"></span>
  		<span class="text">只看内容的评价</span>
  	</div>
  </div>
</template>

<script>
const POSITIVE = 0;
const NEGATIVE = 1;
const ALL = 2;
export default {
  name: 'ratingselect',
  props: {
    ratings: {
      type: Array,
      default() {
        return [];
      }
    },
    selectType: {
      type: Number,
      default: ALL
    },
    onlyContent: {
      type: Boolean,
      default: false
    },
    desc: {
      type: Object,
      default() {
        return {
          all: '全部',
          positive: '满意',
          negative: '不满意'
        };
      }
    }
  },
  methods: {
    select(type, event) {
      if (!event._constructed) {
        return;
      }
      this.selectType = type;
      // 子组件通过 $emit触发父组件的方法 increment   还可以传参   this.$emit('increment' ,this.counter);
      this.$emit('increment', 'selectType', type);
    },
    toggleContent(event) {
      if (!event._constructed) {
        return;
      }
      this.onlyContent = !this.onlyContent;
      this.$emit('increment', 'onlyContent', this.onlyContent);
    }
  },
  computed: {
    positives() {
      return this.ratings.filter((rating) => {
        return rating.rateType === POSITIVE;
      });
    },
    negatives() {
      return this.ratings.filter((rating) => {
        return rating.rateType === NEGATIVE;
      });
    }
  },
  data () {
    return {
    };
  }
};
</script>

<style lang="stylus" style="stylesheet/stylus">
  @import "../../common/stylus/mixin.styl"
  .ratingselect
    .rating-type
      padding: 18px 0
      margin: 0 18px
      border-1px(rgba(7, 17, 27, 0.1))
      font-size: 0
      .block
        display: inline-block
        padding: 8px 12px 4px 12px
        border-radius: 1px
        font-size: 12px
        line-height: 18px
        margin-right: 8px
        color: rgb(77, 89, 93)
        &.active
          color: #fff
        .count
          margin-left: 2px
          line-height: 18px
          font-size: 8px
        &.positive
          background: rgba(0, 160, 220, .2)
          &.active
            background: rgba(0, 160, 220, 1)
        &.negative
          background: rgba(77, 85, 93, .2)
          &.active
            background: rgba(77, 85, 93, 1)
    .switch
      padding: 12px 18px
      line-height: 24px
      border-bottom: rgba(7, 17, 27, .1)
      color: rgb(147, 153, 159)
      font-size: 0
      .icon-check_circle
        display: inline-block
        vertical-align: top
        margin-right: 4px
        font-size: 24px
      .text
        font-size: 12px
    .on
      .icon-check_circle
        color: #00c850
</style>


