<template>
<transition name="move">
  <div v-show="showFlag" class="food" ref="food">
  	<div class="food-content">
  		<div class="image-header">
  			<img :src="food.image">
  			<div class="back" @click="hide">
  				<i class="icon-arrow_lift"></i>
  			</div>
  		</div>
  		<div class="content">
  			<h1 class="title">{{food.name}}</h1>
  			<div class="detail2">
  				<span class="sell-count">月售{{food.sellcount}}份</span><span class="rating">好评率{{food.rating}}%</span>
  			</div>
  			<div class="price">
  				<span class="now">￥{{food.price}}</span>
  				<span class="old" v-show="food.oldPrice">￥{{food.oldPrice}}</span>
  			</div>
        <div class="cartControl-wrapper">
          <cartcontrol :food="food"></cartcontrol>
        </div>
        <transition name="fade">
          <div class="buy" v-show="!food.count || food.count===0" @click.stop.prevent="addFirst(food, $event)">加入购物车</div>
        </transition>
  		</div>
      <split v-show="food.info"></split>
      <div class="info">
        <h1 class="title">商铺信息</h1>
        <p v-show="food.info" class="text">{{food.info}}</p>
      </div>
      <split></split>
      <div class="rating">
        <h1 class="title">商品评价</h1>
        <ratingselect @increment="incrementTotal" :select-type="selectType" :only-content="onlyContent" :desc="desc" :ratings="food.ratings"></ratingselect>
        <div class="rating-wrapper">
          <ul v-show="food.ratings && food.ratings.length">
            <li v-show="needShow(rating.rateType, rating.text)" v-for="rating in food.ratings" class="rating-item border-1px">
              <div class="user">
                <span class="name">{{rating.username}}</span>
                <img width="12" height="12" :src="rating.avatar" class="avatar">
              </div>
              <!-- 给rating.rateTime应用formatDate模版 -->
              <div class="time">{{rating.rateTime | formatDate}}</div>
              <p class="text">
                <!-- 如果ratingType是0class就是icon-thumb_up否则就是dowm -->
                <span :class="{'icon-thumb_up': rating.rateType===0, 'icon-thumb_down': rating.rateType===1}"></span>{{rating.text}}
              </p>
            </li>
          </ul>
          <div class="no-rating" v-show="!food.ratings || !food.ratings.length">暂无评价</div>
        </div>
      </div>
  	</div>
  </div>
 </transition>
</template>

<script>
// 注意from后一定要是小写
import Vue from 'vue';
import BScroll from 'better-scroll';
import cartcontrol from 'components/cartcontrol/cartcontrol';
import split from 'components/split/split';
import ratingselect from 'components/ratingselect/ratingselect';
// 引入function方法的时候要加花括号
import {formatDate} from 'common/js/date';

// const POSITIVE = 0;
// const NEGATIVE = 1;
const ALL = 2;

export default {
  name: 'food',
  props: {
    food: {
      type: Object
    }
  },
  methods: {
    // 父组件可以调用子组件方法
    show() {
      this.showFlag = true;
      this.selectType = ALL;
      this.onlyContent = false;
      this.$nextTick(() => {
        if (!this.scroll) {
          this.scroll = new BScroll(this.$refs.food, {click: true});
        } else {
          this.scroll.refresh();
        }
      });
    },
    hide() {
      this.showFlag = false;
    },
    addFirst(food, event) {
      if (!event._constructed) {
        return;
      }
      Vue.set(this.food, 'count', 1);
    },
    incrementTotal(type, data) {
      this[type] = data;
      this.$nextTick(() => {
        this.scroll.refresh();
      });
    },
    // 按照需求展示数据
    needShow(type, text) {
      // 没有评价内容并且只显示有内容评价
      if (this.onlyContent && !text) {
        return false;
      }
      // 选择类型是全部查看返回true
      if (this.selectType === ALL) {
        return true;
      } else {
        // type设置为选择的评价类型
        return type === this.selectType;
      }
    }
  },
  data () {
    return {
      showFlag: false,
      selectType: ALL,
      onlyContent: false,
      desc: {
        all: '全部',
        positive: '推荐',
        negative: '吐槽'
      }
    };
  },
  filters: {
   formatDate(time) {
    // 把时间戳转化为北京事件
    let date = new Date(time);
    return formatDate(date, 'yyyy-MM-dd hh:mm');
   }
  },
  components: {
    cartcontrol,
    split,
    ratingselect
  }
};
</script>

<style lang="stylus" style="stylesheet/stylus">
@import '../../common/stylus/mixin.styl'
  .food
    position: fixed
    left: 0
    top: 0
    bottom: 48px
    z-index: 30
    width: 100%
    background: #fff
    transition: all .4s linear
    -moz-transition: all .4s linear/* Firefox 4 */
    -webkit-transition: all .4s linear/* Safari 和 Chrome */
    -o-transition: all .4s linear/* Opera */
    &.move-transition
      transform: translate3d(0, 0, 0)
      -ms-transform: translate3d(0, 0, 0)
      -moz-transform: translate3d(0, 0, 0)
      -webkit-transform: translate3d(0, 0, 0)
      -o-transform: translate3d(0, 0, 0)
    &.move-enter, &.move-leave-active
      transform: translate3d(100%, 0, 0)
      -ms-transform: translate3d(100%, 0, 0)
      -moz-transform: translate3d(100%, 0, 0)
      -webkit-transform: translate3d(100%, 0, 0)
      -o-transform: translate3d(100%, 0, 0)
    .image-header
      position: relative
      width: 100%
      height: 0
      padding-bottom: 100%
      img
        position: absolute
        top: 0
        left: 0
        width: 100%
        height: 100%
      .back
        position: absolute
        top: 10px
        left: 0
        .icon-arrow_lift
          display: block
          padding: 10px
          font-size: 20px
          color: #fff
    .content
      position: relative
      padding: 18px
      .title
        line-height: 14px
        margin-bottom: 8px
        font-size: 14px
        font-weight: 700
        color: rgb(7, 17, 27)
      .detail2
        margin-bottom: 18px
        line-height: 10px
        height: 10px
        font-size: 0
        .sell-count, .rating
          font-size: 10px
          color: rgb(147, 153, 159)
        .sell-count
          margin-right: 12px
      .price
        font-weight: 700
        line-height: 24px
        .now
          margin-right: 8px
          font-size: 14px
          color: rgb(240, 20, 20)
        .old
          text-decoration: line-through
          font-size: 10px
          color: rgb(147, 153, 159)
      .cartControl-wrapper
        position: absolute
        right: 12px
        bottom: 12px
      .buy
        position: absolute
        right: 18px
        bottom: 18px
        z-index: 10
        height: 24px
        line-height: 24px
        padding: 0 12px
        box-sizing: border-box
        border-radius: 12px
        font-size:10px
        color: #fff
        background: rgb(0, 160, 225)
    .info
      padding: 18px
      .title
        line-height: 14px
        margin-bottom: 6px
        font-size: 14px
        color: #0a141e
      .text
        line-height: 24px
        padding: 0 8px
        font-size: 12px
        font-weight: 200
        color: #5f666d
    .rating
      padding-top: 18px
      .title
        line-height: 14px
        margin-left: 18px
        font-size: 14px
        color: rgb(7, 17, 27)
      .rating-wrapper
        padding: 0 18px
        .rating-item
          position: relative
          padding: 16px 0
          border-1px(rgba(7, 17, 27, .1))
          .user
            position: absolute
            right: 0
            top: 16px
            line-height: 12px
            font-size: 0
            .name
              display: inline-block
              vertical-align: top
              color: rgb(147, 153, 159)
              font-size: 10px
              margin-right: 6px
            .avatar
              border-radius: 50%
          .time
            line-height: 12px
            font-size: 10px
            color: rgb(7, 17, 27)
            margin-bottom: 6px
          .text
            line-height: 16px
            font-size: 12px
            color: rgb(7, 17, 27)
            .icon-thumb_up, .icon-thumb_down
              line-height: 16px
              margin-right: 4px
              font-size: 12px
            .icon-thumb_up
              color: rgb(0, 160, 220)
            .icon-thumb_down
              color: rgb(147, 153, 159)
        .no-rating
          padding: 16px 0
          font-size: 12px
          color: rgb(147, 153, 159)
</style>


