<template>
  <div class="cartcontrol">
  <transition  name="move">
  	<div class="cart-decrease icon-remove_circle_outline" v-show="food.count>0" @click.stop.prevent="decreaseCart">
  	</div>
  </transition>
  	<div class="cart-count" v-show="food.count>0">{{food.count}}</div>
    <!-- 注意点击事件addCart会默认传入点击这一瞬间事件对象也可直接简写为addCart -->
  	<div class="cart-add icon-add_circle" @click.stop.prevent="addCart"></div>
  </div>
</template>

<script>
import Vue from 'vue';
export default {
  name: 'cartcontrol',
  props: {
    food: {
      type: Object
    }
  },
  methods: {
    addCart(event) {
      if (!event._constructed) {
        return;
      }
      if (!this.food.count) {
        // 如果要添加删除现有对象没有的key值需要使用Vue.set(obj, key , 1),否则观察不到数据的新增的改变
        Vue.set(this.food, 'count', 1);
      } else {
        this.food.count++;
      }
        // 子组件通过 $emit触发父组件的方法 increment并传入当前点击的目标值: event.target为当前dom节点
        this.$emit('increment', event.target);
      },
    decreaseCart(event) {
      if (!event._constructed) {
        return;
      }
      if (this.food.count) {
        this.food.count--;
      }
    }
  },
  data () {
    return {
    };
  }
};
</script>

<style lang="stylus" rel="stylesheet/stylus">
  .cartcontrol
    font-size: 0
    .cart-decrease
      display: inline-block
      padding: 6px
      transition: all .4s linear
      -moz-transition: all .4s linear/* Firefox 4 */
      -webkit-transition: all .4s linear/* Safari 和 Chrome */
      -o-transition: all .4s linear/* Opera */
      font-size: 24px
      line-height: 24px
      color: rgb(0, 160, 220)
      &.move-transition
        opacity: 1
        transform: translate3D(0, 0, 0)
        -ms-transform: translate3D(0, 0, 0)
        -moz-transform: translate3D(0, 0, 0)
        -webkit-transform: translate3D(0, 0, 0)
        -o-transform: translate3D(0, 0, 0)
      &.move-enter, &.move-leave-active
        opacity: 0
        transform: translate3D(24px, 0, 0)
        -ms-transform: translate3D(24px, 0, 0)
        -moz-transform: translate3D(24px, 0, 0)
        -webkit-transform: translate3D(24px, 0, 0)
        -o-transform: translate3D(24px, 0, 0)
  	.cart-count
  		display: inline-block
  		vertical-align: top
  		padding-top: 6px
  		width: 12px
  		line-height: 24px
  		font-size: 10px
  		color: rgb(147, 153, 159)
  		text-align: center
  	.cart-add
  		display: inline-block
  		padding: 6px
  		font-size: 24px
  		line-height: 24px
  		color: rgb(0, 160, 220)
</style>


