<template>
	<div>
   <div class="goods">
      <div class="menu-wrapper" ref="menuWrapper">
        <ul>
          <!-- 
              1.v-el选取元素,v-el已经弃用参考：http://www.cnblogs.com/dupd/p/5904109.html 
              2.在遍历的时候获取到一个index值，如果这个index和当前滚动获取到的index相等那么设置class="current"(把滚动foods列表的index与goods列表的index绑定,参考选项卡)
          -->
          <li v-for="(item, index) in goods" class="menu-item" :class="{'current': currentIndex===index}" @click.stop.prevent="selectMenu(index, $event)">
            <span class="text" border-1px>
              <span v-show="item.type>0" class="icon" :class="classMap[item.type]"></span>{{item.name}}
            </span>
          </li>
        </ul>
      </div>
      <div class="foods-wrapper" ref="foodsWrapper">
        <ul>
          <!-- food-list-hook js选择命名标准在后面添加钩子-hook -->
          <li v-for="item in goods" class="food-list food-list-hook">
            <h1 class="title">{{item.name}}</h1>
            <ul>
              <li @click.stop.prevent="selectFood(food, $event)" v-for="food in item.foods" class="food-item" border-1px>
                <div class="icon">
                  <img width="57" height="57" :src="food.icon" />
                </div>
                <div class="content">
                  <h2 class="name">{{food.name}}</h2>
                  <p class="desc">{{food.description}}</p>
                  <div class="extra">
                    <span class="count">月售{{food.sellCount}}份</span><span>好评率{{food.rating}}%</span>
                  </div>
                  <div class="price">
                    <span class="now">￥{{food.price}}</span><span class="old" v-show="food.oldPrice">￥{{food.oldPrice}}</span>
                  </div>
                  <div class="cartcontrol-wrapper">
                    <cartcontrol @increment="incrementTotal" :food="food"></cartcontrol>
                  </div>
                </div>
              </li>
            </ul>
          </li>
        </ul>
      </div>

      <!-- 购物车 -->
      <shopcart :select-foods="selectFoods" :delivery-price="seller.deliveryPrice" :min-price="seller.minPrice" ref="shopCart"></shopcart>
      <food ref="food" :food="selectedFood"></food>
    </div>
    
  </div>
</template>

<script>
// 参考https://www.npmjs.com/package/better-scroll
import BScroll from 'better-scroll';
import shopcart from 'components/shopcart/shopcart';
import cartcontrol from 'components/cartcontrol/cartcontrol';
import food from 'components/food/food';
import data from 'common/json/data.json';
// const ERR_OK = 0;
export default {
  name: 'goods',
  props: {
    seller: {
      type: Object
    }
  },
  data() {
    return {
      goods: [],
      foods: [],
      listHeight: [],
      scrollY: 0,
      selectedFood: {}
    };
  },
  computed: {
    currentIndex() {
      for (let i = 0; i < this.listHeight.length; i++) {
        let height1 = this.listHeight[i];
        let height2 = this.listHeight[i + 1];
        if (!height2 || (this.scrollY >= height1 && this.scrollY < height2)) {
          return i;
        }
      }
      return 0;
    },
    // 遍历出每一个食物,一旦食物属性发生改变就更新食物数组
    selectFoods() {
      let foods = [];
      this.goods.forEach((good) => {
        good.foods.forEach((food) => {
          // 有食物被选中才去执行更新数组
          if (food.count) {
            foods.push(food);
          }
        });
      });
      return foods;
    }
  },
  created() {
    this.classMap = ['decrease', 'discount', 'special', 'invoice', 'guarantee'];
    // this.$http.get('/api/goods').then((response) => {
    //   response = response.body;
    //   if (response.errno === ERR_OK) {
    //     this.foods = this.goods = response.data;
    //     // 渲染完后在执行重新计算高度 callback回调函数可以理解为setInterval
    //     this.$nextTick(() => {
    //       this._initScroll();
    //       this._calculateHeight();
    //     });
    //   }
    // });
      this.goods = data.goods;
      this.$nextTick(() => {
        this._initScroll();
        this._calculateHeight();
      });
  },
  methods: {
    // 初始化 滚动的同时能够获取到滚动的位置
    _initScroll() {
      // 设置默认事件点击为不阻止
      this.menuScroll = new BScroll(this.$refs.menuWrapper, {click: true});
      // probeType: 1 监听事件的触发时间，1为即时触发，3为延迟到事件完毕后触发
      this.foodsScroll = new BScroll(this.$refs.foodsWrapper, {probeType: 3, click: true});
      // 绑定scroll事件在滚动过程中监控滚动的位置
      this.foodsScroll.on('scroll', (pos) => {
        // 一定要取整取绝对值
        this.scrollY = Math.abs(Math.round(pos.y));
      });
    },
    // 获取每个li的高度并push进listheight
    _calculateHeight() {
      let foodList = this.$refs.foodsWrapper.getElementsByClassName('food-list-hook');
      let height = 0;
      // 必须补全从0开始，不然给li加current的顺序错误
      this.listHeight.push(height);
      for (let i = 0; i < foodList.length; i++) {
        let item = foodList[i];
        height += item.clientHeight;
        this.listHeight.push(height);
      }
    },
    // 选择方法 点击滚动到相应区域
    selectMenu(index, event) {
      // 如果监听到的不是移动端的事件构造函数就不执行跳出
      if (!event._constructed) {
        return;
      }
      let foodList = this.$refs.foodsWrapper.getElementsByClassName('food-list-hook');
      let el = foodList[index];
      // 滚动到foodList[index]相应位置 给foodsScroll派发scrollToElement事件，执行事件300
      this.foodsScroll.scrollToElement(el, 300);
    },
    // 触发shopcart的drop方法并传入目标值
    incrementTotal(target) {
      // 优化性能，异步执行下落动画
      this.$nextTick(() => {
        this.$refs.shopCart.drop(target);
      });
    },
    selectFood(food, event) {
      if (!event._constructed) {
        return;
      }
      this.selectedFood = food;
      this.$refs.food.show();
    }
  },
  components: {
    shopcart,
    cartcontrol,
    food
  }
};
</script>

<style lang="stylus" rel="stylesheet/stylus">
  @import "../../common/stylus/mixin";
  /*定义视口高度即可视区高*/
  .goods
    display: -webkit-flex
    display: flex
    display: -moz-box
    display: -ms-flexbox
    display: -webkit-box
    position: absolute
    top: 174px
    bottom: 46px
    width: 100%
    overflow: hidden
    .menu-wrapper
      -webkit-flex: 0 0 80px
      -ms-flex: 0 0 80px
      flex: 0 0 80px
      -webkit-box-flex: 0 0 80px
      -moz-box-flex: 0 0 80px
      width: 80px
      background: #f3f5f7
      .menu-item
        display: table
        width: 80px
        height: 54px
        line-height: 14px
        .text
          border-none()
        .text
          display: table-cell
          width: 56px
          padding: 0 12px
          vertical-align: middle
          border-1px(rgba(7,17,27,.1))
          font-size: 12px
          .icon
            display: inline-block
            width: 12px
            height: 12px
            vertical-align: top
            margin-right:2px
            background-size: 12px 12px
            background-repeat: no-repeat
            &.decrease
              bg-image('decrease_3')
            &.discount
              bg-image('discount_3')
            &.guarantee
              bg-image('guarantee_3')
            &.invoice
              bg-image('invoice_3')
            &.special
              bg-image('special_3')
      .current
        position: relative
        margin-top: -1px
        background: #fff
        font-weight: 700
    .foods-wrapper
      -webkit-flex: 1        /* Chrome */
      -ms-flex: 1             /* IE 10 */
      flex: 1                /* NEW, Spec - Opera 12.1, Firefox 20+ */
      -webkit-box-flex: 1     /* OLD - iOS 6-, Safari 3.1-6 */
      -moz-box-flex: 1       /* OLD - Firefox 19- */ 
      .title
        padding-left: 14px
        height: 26px
        line-height: 26px
        border-left: 2px solid #d9dde1
        font-size: 12px
        color: rgb(147, 153, 159)
        background: #f3f5f7
      .food-item
        display: -webkit-flex
        display: flex
        display: -moz-box
        display: -ms-flexbox
        display: -webkit-box
        margin: 18px
        padding-bottom: 18px
        border-1px(rgba(7, 17, 27, .1))
        &:last-child
          margin-bottom: 0
          border-none()
        .icon
          -webkit-flex: 0 0 57px
          -ms-flex: 0 0 57px
          flex: 0 0 57px
          -webkit-box-flex: 0 0 57px
          -moz-box-flex: 0 0 57px
          margin-right: 10px
        .content
          -webkit-flex: 1
          -ms-flex: 1
          flex: 1
          -webkit-box-flex: 1
          -moz-box-flex: 1
          .name 
            margin: 2px 0 8px 0
            height: 14px
            line-height: 14px
            font-size: 14px
            color: rgb(7, 17, 27)
            font-weight: 700
          .desc, .extra
            line-height: 10px
            font-size: 12px
            color: rgb(147, 153, 159)
          .desc
            margin-bottom: 8px
            line-height: 12px
          .extra
            .count
              margin-right: 12px
          .price span
            font-weight: 700
            line-height: 24px
            &.now
              margin-right: 8px
              font-size: 14px
              color: rgb(240, 20, 20)
            &.old
              text-decoration: line-through
              font-size: 10px
              color: rgb(147, 153, 159)
          .cartcontrol-wrapper
            position: absolute
            right: 0
            bottom: 12px
</style>


