<template>
	<div class="header">
		<div class="content-wrapper">
			<div class="avatar">
				<!--必须使用计算属性，不然就是src="seller.avatar",读取不到正确图片地址-->
				<img width="64" height="64" :src="seller.avatar" />
			</div>
			<div class="content">
				<div class="title">
					<span class="brand"></span>
					<span class="name">{{seller.name}}</span>
				</div>
				<div class="description">
					{{seller.description}}/{{seller.deliveryTime}}分钟送达
				</div>
				<!--判断是否有活动，没有就没有这个div-->
				<div class="supports" v-if="seller.supports">
					<!--计算属性-->
					<span class="icon" :class="classMap[seller.supports[0].type]"></span>
					<span class="text">{{seller.supports[0].description}}</span>
				</div>
			</div>
			<div v-if="seller.supports" class="supports-count" @click="showDetail">
				<span class="count">{{seller.supports.length}}个</span>
				<i class="icon-keyboard_arrow_right"></i>
			</div>
		</div>
		<div class="bulletin-wrapper" @click="showDetail">
			<span class="bulletin-title"></span><span class="bulletin-text">{{seller.bulletin}}</span>
			<span class="icon-keyboard_arrow_right"></span>
		</div>
		<div class="background">
			<img :src="seller.avatar" width="100%" height="100%" />
		</div>
		<transition name="fade">
			<div v-show="detailShow" class="detail">
				<div class="detail-wrapper clearfix">
					<div class="detail-main">
						<h1 class="name">{{seller.name}}</h1>
						<div class="star-wrapper">
							<star :size="48" :score="seller.score"></star>
						</div>
						<div class="title">
							<div class="line"></div>
							<div class="text">优惠信息</div>
							<div class="line"></div>
						</div>
						<ul v-if="seller.supports" class="supports">
							<!-- v-for循环参数，两个参数，一个item一个index -->
							<li class="supports-item" v-for="(item, index) in seller.supports">
								<span class="icon" :class="classMap[seller.supports[index].type]"></span>
								<span class="text">{{seller.supports[index].description}}</span>
							</li>
						</ul>
						<div class="title">
							<div class="line"></div>
							<div class="text">商家公告</div>
							<div class="line"></div>
						</div>
						<div class="bulletin">
							<p class="content">{{seller.bulletin}}</p>
						</div>
					</div>
				</div>
				<div class="detail-close" @click="hideDetail">
					<i class="icon-close"></i>
				</div>
			</div>
		</transition>
	</div>
</template>

<script>
import star from 'components/star/star';
export default {
  name: 'header',
  props: {
    seller: {
      // 接收传过来的数据一定要定义类型
      type: Object
    }
  },
  methods: {
    showDetail() {
    // 注意一定要加this否则undefind参考小程序语法
      this.detailShow = true;
    },
    hideDetail() {
      this.detailShow = false;
    }
  },
  created() {
    this.classMap = ['decrease', 'discount', 'special', 'invoice', 'guarantee'];
  },
  data() {
    return {
      detailShow: false
    };
  },
  components: {
    star
  }
};
</script>
<style lang="stylus" rel="stylesheet/stylus">
  @import "../../common/stylus/mixin";
  .header
  	position: relative
  	background: rgba(7, 17, 27, .5)
  	color: #fff
  	overflow: hidden
  	.content-wrapper
  		position: relative
  		padding: 24px 12px 18px 24px
  		font-size: 0
  		.avatar
  			display: inline-block
  			margin-right: 16px
  			vertical-align: top
  			img
  				border-radius: 2px
  		.content
  			display: inline-block
  			.title
  				margin: 2px 0 8px 0
  				.brand
  					display: inline-block
  					vertical-align: top
  					width: 30px
  					height: 18px
  					bg-image('brand')
  					background-size: 30px 18px
  					background-repeat: no-repeat
				.name
					margin-left: 6px
					font-size: 16px
					line-heiht: 18px
					font-w
					eight: bold
			.description
				margin-bottom: 10px
				line-height: 12px
				font-size: 12px
			.supports
				.icon
					display: inline-block
					width: 12px
					height: 12px
					vertical-align: top
					margin-right: 4px
					background-size: 12px 12px
					background-repeat: no-repeat
					&.decrease
						bg-image('decrease_1')
					&.discount
						bg-image('discount_1')
					&.guarantee
						bg-image('guarantee_1')
					&.invoice
						bg-image('invoice_1')
					&.special
						bg-image('special_1')
				.text
					line-height: 14px
					font-size: 10px
		.supports-count
			position: absolute
			right: 12px
			bottom: 14px
			padding: 0 8px
			height: 24px
			line-height: 24px
			border-radius: 14px
			background: rgba(0, 0, 0, .2)
			text-align: cente
			.count
				vertical-align: top
				font-size: 10px
			.icon-keyboard_arrow_right
				margin-left: 2px
				line-height: 24px
				font-size: 10px
	.bulletin-wrapper
		position: relative
		height: 28px
		line-height: 28px
		padding: 0 22px 0 12px
		white-space: nowrap
		overflow: hidden
		text-overflow: ellipsis
		background: rgba(7, 27, 27, .2)
		.bulletin-title
			display: inline-block
			vertical-align: top
			width: 22px
			height: 12px
			margin-top: 8px
			bg-image('bulletin')
			background-size: 22px 12px
			background-repeat: no-repeat
		.bulletin-text
			vertical-align: top
			margin: 0 4px
			font-size: 10px
		.icon-keyboard_arrow_right
			position: absolute
			font-size: 10px
			right: 12px
			top: 8px
	.background
		position: absolute
		top: 0
		left: 0
		width: 100%
		height: 100%
		z-index: -1
		filter: blur(10px)
		-webkit-filter: blur(10px)
		-moz-filter:blur(3px)
		-o-filter:blur(3px)
	.fade-enter-active, .fade-leave-active
		transition: opacity .5s
	.fade-enter, .fade-leave-active
		opacity: 0
	/*Sticky footers*/
	.detail
		position: fixed
		z-index: 100
		top: 0
		left: 0
		width: 100%
		height: 100%
		overflow: auto
		background: rgba(7, 17, 27, .8)
		filter: blur(0)
		backdrop-filter: blur(3px)
		.detail-wrapper
			width: 100%
			min-height: 100%
			.detail-main
				margin-top: 40px
				padding-bottom: 64px
				.name
					line-height: 16px
					text-align: center
					font-size: 16px
					font-weight: 700
				.star-wrapper
					margin-top: 18px
					padding: 2px 0
					text-align: center
				.title
					/*注意flex兼容写法*/
					display: -webkit-flex
					display: flex
					display: -moz-box
					display: -ms-flexbox
					display: -webkit-box
					width: 80%
					margin: 28px auto 24px auto
					.line
						-webkit-flex: 1
						-ms-flex: 1
						flex: 1
						-webkit-box-flex: 1
						-moz-box-flex: 1
						position: relative
						top: -6px
						border-bottom: 1px solid rgba(255, 255, 255, .2)
					.text
						padding: 0 12px
						font-weight: 700
						font-size: 14px
				.supports
					width: 80%
					margin: 0 auto
					.supports-item
						padding: 0 12px
						margin-bottom: 12px
						font-size: 0
						&:last-child
							margin-bottom: 0
						.icon
							display: inline-block
							width: 16px
							height: 16px
							vertical-align: top
							margin-right: 6px
							background-size: 16px 16px
							background-repeat: no-repeat
						.decrease
							bg-image('decrease_1')
						.discount
							bg-image('discount_1')
						.guarantee
							bg-image('guarantee_1')
						.invoice
							bg-image('invoice_1')
						.special
							bg-image('special_1')
						.text
							line-height: 16px
							font-size: 12px
				.bulletin
					width: 80%
					margin: 0 auto
					.content
						padding: 0 12px
						line-height: 24px
						font-size: 12px
		.detail-close
			position: relative
			width: 32px
			height: 32px
			margin: -64px auto 0 auto
			clear: both
			font-size: 32px
</style>


