export function saveTolocal(id, key, value) {
  // node里面没有localStorage必须在浏览器window里面使用
  let seller = window.localStorage.__seller__;
  // 如果localStorage没有seller对象，创建seller对象
  if (!seller) {
    seller = {};
    seller[id] = {};
  } else {
    // 如果localStorage里面有seller对象，转化成json
    seller = JSON.parse(seller);
    // 如果seller对象里面没有id key值，创建
    if (!seller[id]) {
      seller[id] = {};
    }
  }
  // 给seller.id.key赋值
  seller[id][key] = value;
  // 把seller对象解析为字符串
  window.localStorage.__seller__ = JSON.stringify(seller);
};

export function loadFromLocal(id, key, def) {
  let seller = window.localStorage.__seller__;
  if (!seller) {
    return def;
  }
  // 返回id为12345的对象 {"12345":{"favorite":true},"undefined":{"favorite":true}}
  seller = JSON.parse(seller)[id];
  if (!seller) {
    return def;
  }
  // true
  let ret = seller[key];
  return ret || def;
}



